import { ErrorAccessor } from '../state/ErrorAccessor';
import { ErrorHandler } from './ErrorHandler';
import { ErrorResolution } from '../state/ErrorResolution';
import { ErrorCodesMap } from '@wix/error-handler-core/types';

export interface CreateErrorResolutionParams {
  readonly errorAccessor: ErrorAccessor;
  readonly errorCodesMap: ErrorCodesMap | null | undefined;
}

export function createErrorResolution(
  errorHandler: ErrorHandler,
  params: CreateErrorResolutionParams,
) {
  const { commonErrorsMapState, onlineManager } = errorHandler;

  const { errorAccessor, errorCodesMap } = params;

  const errorResolution = new ErrorResolution({
    errorAccessor,
    errorCodesMap,
    commonErrorsMapState,
    onlineManager,
  });

  errorResolution.resolve();

  return errorResolution;
}
